<template>
  <div class="container">
    <table>
      <caption>{{tableContent.thead}}</caption>
      <tr v-for="item in tableContent.tbody">
        <td width='50%'>{{item.key}}</td>
        <td>{{item.value}}</td>
      </tr>
    </table>
  </div>
</template>

<script>

export default {
  name: 'el-table',
  props: {
    tableContent: {
      type: Object,
      default: {
        thead: '《电影》',
        tbody: [
          { key: '上映时间', value: '2017年1月28日' }
        ]
      }
    }
  },
  data () {
    return {
    }
  }
}
</script>

<style lang="postcss">
.container {
  width: 9.290666666666667rem;
  margin: 0.4rem auto;
}
table {
  font-size: 12px;
  color: #4A4A4A;
  letter-spacing: 1px;
  border: 1px solid #9B9B9B;
  border-collapse: collapse;
  caption {
    line-height: 28px;
    border: 1px solid #9B9B9B;
    border-bottom: none;
  }
  td:nth-child(1) {
    text-align: center;
  }
  td {
    line-height: 17px;
    border: 1px solid #9B9B9B;
    padding: 0.14400000000000002rem 0.26666666666666666rem;
  }
}
</style>



