<template>
<div class="container">
  <h1 class="title">{{article.title}}<span>{{article.titleInEng}}</span></h1>
  <div class="content" v-for="contentItem in article.content">
    <ul v-show=contentItem.title>
      <li>{{contentItem.title}}</li>
    </ul>
    <div>
      <p v-for="item in contentItem.items">{{item}}</p>
      <el-table  v-show=contentItem.hasTable :tableContent=item v-for="item in tableContent"></el-table>
    </div>
  </div>
</div>
</template>

<script>
import table from './table.vue'

export default {
  name: 'intro-item',

  components: {
    'el-table': table
  },
  props: {
    article: {
      type: Object,
      default: {
        title: '',
        titleInEng: '',
        content: [
          {
            title: '',
            hasTable: false,
            items: [
              ''
            ]
          }
        ]
      }
    }
  },
  data() {
    return {
      tableContent: [{
        thead: '《西游伏妖篇》',
        tbody: [
          { key: '上映时间', value: '2017年1月28日' },
          { key: '导演', value: '徐克' },
          { key: '主演', value: '吴亦凡、林更新、姚晨、林允' },
          { key: '备注', value: '17年春节档票房冠军，截至17年2月底累计票房突破16.4亿' }
        ]
      }, {
        thead: '《美人鱼》',
        tbody: [
          { key: '上映时间', value: '2016年2月8日' },
          { key: '导演', value: '周星驰' },
          { key: '主演', value: '邓超、林允、张雨绮、罗志祥' },
          { key: '备注', value: '华语电影票房冠军，创造了33.9亿票房纪录' }
        ]
      }, {
        thead: '《既然青春留不住》',
        tbody: [
          { key: '上映时间', value: '2015年10月23日' },
          { key: '导演', value: '田蒙' },
          { key: '主演', value: '张翰、陈乔恩' },
          { key: '备注', value: '改编自小说《当菠菜遇上空心菜》，入围2015年度加拿大蒙特利尔电影节展映单元' }
        ]
      }, {
        thead: '《天将雄师》',
        tbody: [
          { key: '上映时间', value: '2015年2月19日' },
          { key: '导演', value: '李仁港' },
          { key: '主演', value: '成龙、约翰·库萨克、阿德里安·布劳迪、崔始源 、林鹏' },
          { key: '备注', value: '15年春节档首日票房冠军' }
        ]
      }]
    }
  }
}
</script>

<style lang="postcss">
  .container {
    color: #4A4A4A;
    letter-spacing: 0.75px;
    .title {
      margin-top: 0.5066666666666667rem;
      font-size: 15px;
      line-height: 19px;
      font-weight: 500;
      span {
        font-weight: 400;
        font-size: 10px;
        margin-left: 0.13333333333333333rem;
      }
    }
    .content {
      line-height: 19px;
      ul li {
        margin-left: 0.4533333333333333rem;
        padding: 5px 0;
        font-weight: 500;
        list-style-type: disc;
      }
      p {
        font-weight: 400;
      }
    }
  }
</style>



